import { request } from 'utils';

const prefix = '/api/education';

export const getAllGradeNote = params =>
  request({
    prefix,
    url: '/GradeNote',
    params: {
      ...params,
      isPagingEnabled: false,
    },
    method: 'GET',
  });

export const getGradeNoteById = (id, dataEdit) =>
  request({
    prefix,
    url: `/GradeNote/${id}`,
    params: {
      ...dataEdit,
    },
    method: 'GET',
  });

export const createGradeNote = newGradeNote =>
  request({
    prefix,
    url: `/GradeNote`,
    data: {
      ...newGradeNote,
    },
    method: 'POST',
  });

export const updateGradeNote = dataGradeNote =>
  request({
    prefix,
    url: `/GradeNote`,
    data: {
      ...dataGradeNote,
    },
    method: 'PUT',
  });

export const deleteGradeNote = id =>
  request({
    prefix,
    url: `/GradeNote/${id}`,
    method: 'DELETE',
  });



