import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message, Spin } from 'antd';
import to from 'await-to-js';
import { getGradeNoteById, updateGradeNote, createGradeNote } from 'services/gradeNote';
// import routeConstant from 'routes/route/routeConstant';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormGradeNote' })
class FormGradeNote extends Component {
  state = {
    systemId: false,
    initialValue: {
      userTypeId: '3',
      disabled: false,
    },
    loading: false,
    userId: '',
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.getUser(queryParamId);
    }
  };
  getUser = async queryParamId => {
    this.setState({
      userId: queryParamId,
      loading: true,
    });
    const [error, response] = await to(getGradeNoteById(queryParamId));
    if (error) return;
    const initialValue = handleInitialValue(response.data);
    this.setState({ initialValue, loading: false });
  };

  handleSubmit = e => {
    const { userId } = this.state;
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        if (userId) {
          const [error] = await to(updateGradeNote(values));
          if (error) return;
          message.success('Chỉnh sửa thành công');
          this.props.history.push('/nhap-diem');
        } else {
          const [error] = await to(createGradeNote(values));
          if (error) return;
          message.success('Thêm thành công');
          this.props.history.push('/nhap-diem');
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;

    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId } = this.state;
    return (
      <Card title={systemId ? 'Cập nhật nhập điểm' : 'Thêm nhập điểm'}>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item style={{ display: 'none' }} hasFeedback label="Họ" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                })(<Input />)}
              </Form.Item>
              <Form.Item hasFeedback label="Ghi chú nhập điểm" {...formItemLayout}>
                {getFieldDecorator('label', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormGradeNote {...props} />
  </Page>
));



